.box {
    height: 30px;
    width: 30px;
    border-left: 0.5px solid black;
    border-top: 0.5px solid black;
    display: block;
    cursor: pointer;
  }

  @media screen and (max-width: 414px) {
    .box {
      height: 15px;
      width: 15px;
      display: block;
      cursor: pointer;
    }
  
  }